/**
 * 
 *
 * @format
 * @param value
 * @param callback
 * @returns {[undefined, undefined]}
 */

export const makePair = (value, ...callbacks) => {
  return [
    ...callbacks.map(callback => {
      if (typeof callback === 'function') {
        return callback(value);
      } else {
        return value;
      }
    }),
  ];
};
